import React from "react";
import Helmet from "react-helmet";
import { View, Sidebar } from "@rk/design-system";
import { default as SidebarContent } from "./Sidebar";
import Main from "./Main";

const Page = ({
  title,
  header,
  subheader,
  metadata,
  children,
  titleLinkHandler
}) => {
  return (
    <>
      {title && <Helmet title={`${title} | ${metadata.title}`} />}
      <Sidebar
        className="linear-bg"
        width={280}
        sidebar={
          <View mt={[0, 0, "auto"]} mx={["auto", "auto", 0]} p={"1rem"}>
            <SidebarContent
              metadata={metadata}
              titleLinkHandler={titleLinkHandler}
            />
          </View>
        }
      >
        <Main
          metadata={metadata}
          header={header}
          subheader={subheader}
          children={children}
        />
      </Sidebar>
    </>
  );
};

export default Page;
